import React from 'react'
import { Layout, Space, Button, Dropdown } from 'antd'
import type { MenuProps } from 'antd';
import Logo from '../assets/out.png'
import '../App.css';

const { Header } = Layout

const items: MenuProps['items'] = [
  {
    key: '1',
    label: (
      <a target="_blank" rel="noopener noreferrer" href="/portfolio">
        General
      </a>
    ),
  },
  {
    key: '2',
    label: (
      <a target="_blank" rel="noopener noreferrer" href="/portfolio?q=Art">
        Art
      </a>
    )
  },
  {
    key: '3',
    label: (
      <a target="_blank" rel="noopener noreferrer" href="/portfolio?q=Design">
        Design
      </a>
    )
  },
  {
    key: '4',
    label: (
      <a target="_blank" rel="noopener noreferrer" href="/portfolio?q=Software">
        Software
      </a>
    )
  },
  {
    key: '5',
    label: (
      <a target="_blank" rel="noopener noreferrer" href="/portfolio?q=EEE">
        EEE
      </a>
    )
  },
];

const Nav = () => {
  return (
    <Header
      style={{
        position: 'sticky',
        top: 0,
        zIndex: 1,
        width: '100%',
        display: 'flex',
        alignItems: 'center',
        background: 'transparent'
      }}
      className='mainHeader'
    >
      <Space className='header' direction='horizontal'>
      <Dropdown menu={{ items }} placement='bottom'>
        <a onClick={(e) => e.preventDefault()} className='headerLink'>
          <span style={{fontSize:14, fontFamily:'Syne', paddingBottom:3}}>PORTFOLIO</span>
        </a>
      </Dropdown>
        <a href='https://outsu.github.io/'><div style={{width: 78, height:35, background:`url(${Logo})`, backgroundSize:'cover'}}/></a>
        <Button ghost className='headerLink'>CONTACT</Button>
      </Space>
    </Header>
  )
}

export default Nav