import styled from "@emotion/styled"
import { Button, Col, Flex, Input, Row, Space, Typography } from "antd";
import React, { useRef } from 'react';
import emailjs from '@emailjs/browser';
import TextArea from "antd/es/input/TextArea";
import { Footer } from "antd/es/layout/layout";
import { GithubOutlined, LinkedinOutlined, LoadingOutlined, MailOutlined, CheckCircleOutlined } from "@ant-design/icons";
import out from "../assets/out.png";


const { Title, Text } = Typography;


const Page = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`;

const Form = styled.form`
  width: 50%;
  @media (max-width: 768px) {
    width: 100%;
  }
`;

const Link = styled.a`
  color: white;
  :hover {
    color: #0E1514;
  }
`;

const Entry = styled(Input)`
  margin-bottom: 3px;
  opacity: 0.7;
`;

const Contact = () => {

  const form = useRef();
  const [loading, setLoading] = React.useState(false);
  const [success, setSuccess] = React.useState(false);

  const sendEmail = (e) => {
    e.preventDefault();
    setLoading(true);

    emailjs
      .sendForm('service_18zvybm', 'template_jo7hduo', form.current, {
        publicKey: '-yRP5_GD3vQYbfxzU',
      })
      .then(
        () => {
          console.log('SUCCESS!');
          setLoading(false);
          setSuccess(true);
        },
        (error) => {
          console.log('FAILED...', error.text);
          setLoading(false);
        },
      );
  };

  return (
    <Footer id="Contact" className="footer" style={{textAlign: 'center', color: 'white'}}>
      <Row>
      <Col 
          xs={{ flex: '100%' }}
          lg={{ flex: '50%' }}>
        <Page id="Cards">
          <Title level={3} style={{marginBottom:40}}>Contact</Title>
          <Text style={{marginBottom:20}}>Feel free to reach out to me on any of the following platforms:</Text>
          <Row><Link href="https://www.linkedin.com/in/%C3%B6%C4%9F%C3%BCt-su-karag%C3%BCn-635211176/"><LinkedinOutlined/>&nbsp; Öğüt Su Karagün </Link> </Row>
          <Row><Link href="https://www.github.com/outsu"><GithubOutlined/>&nbsp; outsu </Link></Row>
          <Row><Link href="mailto://out@engineer.com"><MailOutlined/>&nbsp; out@engineer.com </Link></Row>
      </Page>
    </Col>
      <Col 
          md={{ flex: '100%' }}
          lg={{ flex: '50%' }}>
      <Page id="Cards" style={{height:268}}>
        {success ? 
        <div style={{display:'ruby', marginBottom:40}}><Title level={3} >Thank you for reaching&nbsp;</Title> <img src={out} style={{height:16}}/><Title level={3} style={{margin:0}}>!</Title> </div> :
        <Title level={3} style={{marginBottom:40}}>Let's get in touch.</Title>}
        {loading ? <LoadingOutlined style={{fontSize: 24}}/> : success ? <CheckCircleOutlined style={{fontSize: 24}}/> : (
          <Form ref={form} onSubmit={sendEmail}>
            <Entry type="text" name="from_name" placeholder="Name" />
            <Entry type="email" name="from_email" placeholder="Email" />
            <TextArea rows={3} style={{marginBottom:5, opacity:0.7}} name="message"/>
            <Entry type="submit" value="Send" />
          </Form>)}
      </Page>
    </Col></Row>
    </Footer>
  )
}

export default Contact